import { getTodaysTime } from '../../utils';
import { dealPassesFilter, dealContainsText, sortByOptions } from '../../postprocess';
import { formatTodaysTime, formatDiscount, elipseFormat } from '../PlaceDetails/PlaceDetails';
import { Directions } from '@material-ui/icons';
import { IconButton } from '@material-ui/core';

const formatDealCount = (deals) => {
  const ndeals = deals.length;
  const nfood = deals.filter(deal => deal.type === 'food').length;
  const ndrink = deals.filter(deal => deal.type === 'drink').length;
  const deal_word = `deal${ndeals > 1 ? 's' : ''}`
  if (nfood > 0 && ndrink > 0) {
    return `${ndeals} ${deal_word} (${nfood} food, ${ndrink} drink)`
  }
  if (nfood > 0) {
    return `${nfood} food ${deal_word}`
  }
  return `${ndrink} drink ${deal_word}`
}


const mkPhotoUrl = (photoRef) => {
  return `https://maps.googleapis.com/maps/api/place/photo?photo_reference=${photoRef}&key=AIzaSyCxfXAPgVM8ownCZdr8dFbQSg76chB7P4s&maxheight=100`;
};

const sortDeals = (dealsFilter, searchText, sortBy, deals) => {
  let passing, failing;
  if (sortByOptions[sortBy] === 'Cheapest Item') {
    passing = deals.filter(d => d.discount_marker === '$');
    failing = deals.filter(d => d.discount_marker !== '$');
    passing.sort((d1, d2) => d1.discount_amount - d2.discount_amount);
  }
  else {
    const f = deal => searchText ? dealContainsText(searchText, deal) : dealPassesFilter(dealsFilter, deal);
    passing = deals.filter(f);
    failing = deals.filter(deal => !f(deal));
  }
  return passing.concat(failing);
}

const Place = ({ place, timeFilter, dealsFilter, searchText, sortBy }) => {
  return (
    <div class="place">
      <div class="image-horizontal-scroll">
        {place.photos?.map(mkPhotoUrl).map((url, i) => (
          <img alt="" src={url} key={i} />
        ))}
      </div>
      <div class="place-details" style={{ position: 'relative' }}>
        <div class="place-name">{place.place_name}</div>
        <div>⭐ {place.rating} ({place.user_ratings_total})</div>
        <div>
          {formatDealCount(place.deals)} · {'$'.repeat(place.price_level + 1)} · {place.distance}
        </div>
        <div>{formatTodaysTime(getTodaysTime(place.times), timeFilter)}</div>
        <IconButton
          aria-label="upload picture" 
          onClick={() => window.open(place.gmap_url, '_blank')}
          style={{ backgroundColor: '#1A73E8', position: 'absolute', right: 20, top: 5 }}
          size='small'
        >
          <Directions style={{color: 'white'}}/>
        </IconButton>
      </div>
      <div class="deals-preview-container">
        <div class="deals-preview">
          {sortDeals(dealsFilter, searchText, sortBy, place.deals).map((deal) => (
            formatDiscount(deal.discount_amount, deal.discount_marker) + ' ' + elipseFormat(35, deal.item)
          )).join(' · ')}
        </div>
      </div>
    </div>
  );
}

export default Place;
