import { doIntersect, singularize, condensedTimeToSeconds, getTodaysTime } from './utils';

export const sortByOptions = ['Number of Deals', 'Popularity', 'Rating', 'Distance', 'Ending Time', 'Cheapest Item'];

export const timeFilterOptions = ['Now', '4:00 PM', '4:30 PM', '5:00 PM', '5:30 PM', '6:00 PM', '6:30 PM'];

export const dealsFilterOptions = ['All Deals', 'Beer', 'Wine', 'Cocktails', 'Margaritas', 'Tacos', 'Meat', 'Seafood', 'Salad', 'Appetizers'];

export const synonyms = {
  'beer': ['draft', 'lone', 'domestic', 'ale', 'ipa', 'pilsner', 'porter'],
  'cocktail': ['martini', 'sangaria', 'margarita', 'mojito', 'daiquiri', 'bloody'],
  'spirit': ['well', 'vodka', 'wiskey', 'shot'],
  'wine': ['bottle', 'sake', 'red', 'white', 'rose'],
  'appetizer': ['dip', 'queso', 'sliders', 'roll', 'chips', 'quesadilla', 'platter', 'bite', 'mini'],
  'meat': ['grilled', 'fried', 'chicken', 'beef', 'burger', 'brisket', 'buffalo', 'bacon', 'steak', 'bbq'],
  'seafood': ['salmon', 'maki', 'crab', 'shrimp', 'calamari', 'tuna', 'tempura', 'oyster']
}

export const getPlaceCompareFcn = (sortBy) => 
  (place1, place2) => {
    const getSortValue = (place, sortBy) => {
      var value;
      if (sortBy === 'Popularity') {
        value = 0;
      }
      sortBy = sortByOptions[sortBy];
      if (sortBy === 'Number of Deals') {
        value = place.deals.length;
      }
      if (sortBy === 'Popularity') {
        value = place.user_ratings_total;
      }
      if (sortBy === 'Rating') {
        value = place.rating;
      }
      if (sortBy === 'Distance') {
        value = -parseFloat(place.distance.split(' ')[0]);
      }
      if (sortBy === 'Ending Time') {
        value = condensedTimeToSeconds(getTodaysTime(place.times).end_time);
      }
      if (sortBy === 'Cheapest Item') {
        const dollarDeals = place.deals.filter(d => d.discount_marker === '$');
        value = -(dollarDeals.length > 0 ? Math.min(...dollarDeals.map(d => d.discount_amount)) : 100);
      }
      return value;
    }
    return getSortValue(place1, sortBy) - getSortValue(place2, sortBy);
  }


const getDealText = (deal) => 
  deal.item
    .replace('.', ' ').replace(',', ' ').replace('/', ' ').replace('&', ' ')
    .toLowerCase().split(' ').map(singularize)

export const dealPassesFilter = (dealsFilter, deal) => {
  let filter = dealsFilterOptions[dealsFilter].toLowerCase();
  if (filter === 'all deals') {
    return true;
  }
  const dealWords = getDealText(deal);
  filter = singularize(filter)
  const filterWords = filter in synonyms ? synonyms[filter] : [];
  filterWords.push(filter);

  return doIntersect(dealWords, filterWords);
}

export const makeDealsFilter = (dealsFilter) =>
  (place) => place.deals.some(deal => dealPassesFilter(dealsFilter, deal));

export const dealContainsText = (text, deal) =>
  getDealText(deal).includes(text.toLowerCase());

export const scoreSearchMatch = (searchText, place) => {
  if (searchText === '') return true;
  searchText = searchText.toLowerCase();
  const placeName = place.place_name.toLowerCase();
  const placeDealsText = place.deals.map(getDealText).join(' ');
  if (placeName === searchText) return 3
  if (placeName.includes(searchText)) return 2
  if (placeDealsText.includes(searchText)) return 1
  return 0;
}

export const makeSearchCompareFcn = (searchText) => 
  (place1, place2) =>
    scoreSearchMatch(searchText, place1) - scoreSearchMatch(searchText, place2)
