import DropDown from './Dropdown';
import { sortByOptions, timeFilterOptions, dealsFilterOptions } from '../../postprocess';

const AppBar = (props) => {

  return (
    <div class="appbar">
      <div class="searchbar">
        <input
          class="text"
          placeholder="Search Place or Deal..."
          style={{ border: 'none', width: '80%', height: '90%', outline: 'none' }}
          value={props.searchText}
          onChange={(e) => props.setSearchText(e.target.value)}
        />
      </div>
      <div class="filterbar">
        <DropDown
          options={sortByOptions}
          selection={props.sortBy}
          setSelection={props.setSortBy}
          textPrompt={'Sort By'}
        />
        <DropDown
          options={timeFilterOptions}
          selection={props.timeFilter}
          setSelection={props.setTimeFilter}
        />
        <DropDown
          options={dealsFilterOptions}
          selection={props.dealsFilter}
          setSelection={props.setDealsFilter}
        />
      </div>
    </div>
  );
}

export default AppBar;
